@import "../utils/variables";
@import "../utils/mixins";

/* resets */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
a {
    color: #000000;
}
html {
    overflow: auto;
}
body {
    overflow: hidden;
    font-family: $mulish;
}
li {
    list-style: none;
  }
a {
    text-decoration: none;
}

/* global */
.flex {
    display: flex;
}
.direction-row {
    flex-direction: row;
}
.direction-column {
    flex-direction: column;
}
.flex-between {
    justify-content: space-between;
}
.flex-center {
    justify-content: center;
}
.flex-around {
    justify-content: space-around;
}
.flex-even {
    justify-content: space-evenly;
}
.flex-wrap {
    flex-wrap: wrap;
}
.align-center {
    align-items: center;
}
.w-100 {
   width: 100%; 
}
.center-x {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}
.center-y {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}
.center {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
.tint {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba($color: #000000, $alpha: 0.5)
}
img {
    width: 100%;
}
.cursor:hover {
    cursor: pointer;
}
.block {
    display: block;
}
.inline-block {
    display: inline-block;
}
.relative {
    position: relative;
}
.absolute {
    position: absolute;
}
.fixed {
    position: fixed;
}
.y-scroll {
    overflow-y: scroll;
}
.text-center {
    text-align: center;
}
.text-right {
    text-align: right;
}

/* only global to application */
body {
    background: $bg-light;
    main {
        margin-bottom: 4rem;
        .text {
            color: $dark;
        }
        .last-updated {
            @include container(small);
            padding: 1rem 0;
            @include media-query-grow(1024px) {
                padding: 2rem 0;
            }
        }
        .component {
            background: $white;
            margin-bottom: 1.5rem;
            padding: 1rem;
            border-radius: 15px;
        }
    }
}